import React from "react";
import "./ContactsStyle.scss";
import { Footer } from "../Footer/Footer";
  let userName;
  let email;
  let password;
  let gender;
  let message;
  function display_details(event) {
    event.preventDefault();
    userName = document.getElementsByName("userName")[0].value;
    email = document.getElementsByName("email")[0].value;
    password = document.getElementsByName("password")[0].value;
    gender = document.getElementsByName("gender")[0].value;
    message = document.getElementsByName("message")[0].value;
    console.log(
      "user name:" +
        userName +
        "\n email:" +
        email +
        "\n password: " +
        password +
        "\n gender: " +
        gender +
        "\n message: " +
        message
    );
  }
  
  export function ContactUs() {
    return (
      <section id="form-block">
        <div className="user-details_form">
          <form id="myForm" onSubmit={display_details}>
            <input
              type={"text"}
              name="userName"
              placeholder="Enter your Name"
              required
            />
            <br />
            <input
              type={"email"}
              name="email"
              placeholder="Enter your email id"
              required
            />
            <br />
            <input
              type={"password"}
              name="password"
              placeholder="Enter your password"
              required
            />
            <br />
            
            <label><input type={"radio"} name="gender" value={"male"} required />Male</label>

            <label><input type={"radio"} name="gender" value={"female"} required />Female</label>
            <br />
            <textarea name="message" placeholder="Enter your message" required></textarea>
            <br />
          
            <label>  <input type={"checkbox"} required />I agree terms and condition</label>
            <br />
            <input type={"submit"} className="formbtn" />
            <br />
            <input type={"reset"} className="formbtn"/>
          </form>
        </div>

       <Footer/>
   </section>
    );
  };






